import Image from "next/image";
import Link from "next/link";
import { notFound } from "next/navigation";
import { projects } from "@/app/data/projects";

type Props = {
  params: {
    id: string;
  };
};

export function generateStaticParams() {
  return projects.map((project) => ({ id: project.id }));
}

export default function ProjectDetailPage({ params }: Props) {
  const project = projects.find((p) => p.id === params.id);

  if (!project) {
    notFound();
  }

  return (
    <main className="mx-auto max-w-5xl px-6 py-16 text-slate-100 space-y-10">
      <Link
        href="/#work"
        className="text-sm font-medium text-emerald-300 transition hover:text-emerald-200"
      >
        ← Back to projects
      </Link>

      <header className="space-y-4">
        {project.subtitle && (
          <p className="font-mono text-xs uppercase tracking-[0.3em] text-emerald-200/80">
            {project.subtitle}
          </p>
        )}
        <h1 className="text-3xl font-semibold">{project.title}</h1>
        <p className="text-lg text-slate-300">{project.description}</p>
      </header>

      <section className="grid gap-10 md:grid-cols-[2fr,1fr]">
        <div className="relative h-72 overflow-hidden rounded-2xl border border-white/10 md:h-96">
          <Image
            src={project.image}
            alt={project.title}
            fill
            className="object-cover"
            priority
          />
          <div className="absolute inset-0 bg-slate-900/30" />
        </div>

        <aside className="space-y-6 rounded-2xl border border-white/10 bg-slate-900/60 p-6">
          <div>
            <h2 className="text-sm font-semibold uppercase tracking-[0.2em] text-slate-400">
              Tech Stack
            </h2>
            <ul className="mt-3 flex flex-wrap gap-2 text-sm text-slate-200">
              {project.tech.map((tech) => (
                <li
                  key={tech}
                  className="rounded-full border border-white/10 px-3 py-1"
                >
                  {tech}
                </li>
              ))}
            </ul>
          </div>

          <div className="space-y-2 text-sm text-slate-300">
            {project.github && (
              <a
                href={project.github}
                target="_blank"
                rel="noopener noreferrer"
                className="block rounded-md border border-white/10 px-3 py-2 transition hover:border-white hover:text-white"
              >
                View source on GitHub →
              </a>
            )}
            {project.external && (
              <a
                href={project.external}
                target="_blank"
                rel="noopener noreferrer"
                className="block rounded-md border border-white/10 px-3 py-2 transition hover:border-white hover:text-white"
              >
                Visit live experience →
              </a>
            )}
          </div>
        </aside>
      </section>
    </main>
  );
}
